.email__modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0 20px 0 20px;
}

.email__modal-form {
  margin: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(92, 91, 91);
  padding: 20px;
  border-radius: 25px;
  max-width: 400px;
  max-height: 400px;
  height: fit-content;
  width: 100%;
}

.email__modal-close-button-image {
  background-image: url("../../images/close-button-modal.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  height: 20px;
  width: 20px;
  border: none;
  position: absolute;
  right: 28px;
  top: 28px;
  cursor: pointer;
}

.email__modal-title {
  font-family: "Teko", sans-serif;
  margin: 0;
  font-size: 60px;
  font-weight: 600;
  color: #919191;
}

.email__modal-letter-m {
  width: 35px;
  height: 35px;
}

.email__modal-input-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 15px;
}

.email__modal-input {
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(92, 91, 91);
  color: white;
  border: none;
  border-bottom: 2px solid #49a246;
  font-size: 1rem;
  padding: 0;
  outline: none;
  resize: none;
}

.email__modal-input::placeholder {
  color: #919191;
}

.email__modal-submit-btn {
  background-color: #49a246;
  color: black;
  font-family: "Teko", sans-serif;
  font-size: 22px;
  font-weight: 300;
  margin: 0 auto;
  width: 100px;
  padding: 8px;
  border-radius: 25px;
  border: none;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .email__modal-title {
    font-size: 50px;
  }
  .email__modal-letter-m {
    width: 30px;
    height: 30px;
  }
}

@media screen and (max-width: 375px) {
  .email__modal-title {
    font-size: 40px;
  }

  .email__modal-letter-m {
    width: 25px;
    height: 25px;
  }

  #message-input {
    height: 175px;
  }
}
